import {createApp} from 'vue'
import App from './App.vue'
import VueSocketIO from 'vue-3-socket.io'
import SocketIO from 'socket.io-client'

//const options = { path: '/' };
const app = createApp(App);
app.use(new VueSocketIO({
    debug: true,
    connection: SocketIO('/queue'),
}));
app.mount('#app');