<template>
  <h1>Simple chat</h1>
  <span>{{uid}}</span>
  <div class="wrap">
    
    <div class="inps">
      <textarea v-model="resp"></textarea>
    </div>
    <div class="inps">
      <input type="text" v-model="message">
      <input type="button" value="send" @click="clickButton(message)">
      <input type="button" value="send in room" @click="send_in_room(message)">
      <input type="text" v-model="send_room">
      <input type="button" value="join room" @click="join(send_room)">
      <input type="button" value="close connenct" @click="disconnected_server">
    </div>
    
  </div>
</template>

<script>
import { defineComponent } from "@vue/runtime-core"


export default defineComponent({
  name: 'App',
  components: {
    
  },
  created() {
    
  },
  data() {
    return {
      resp: '',
      data: '',
      message: '',
      uid: '',
      current_room: '',
      send_room: '',
    }
  },
  sockets: {
    api_response(data) {
      this.resp = this.resp + '\nclient' + data.uid + ": " + data.data;
      console.log(data)
    },
    uid_client(data) {
      this.uid = data.uid
      this.clickButton("conncect client: " + data.uid)
      console.log(data.uid)
    }
  },
  methods: {
    clickButton(message) {
      // $socket is socket.io-client instance
      this.$socket.emit("broadcast_message", message);
    },
    join(message) {
      this.current_room = message;
      this.$socket.emit("join", message);
      this.broadcast_room("client join in room: " + this.uid)
    },
    send_in_room(message) {
      this.$socket.emit("send_room_message", {data: message, room: this.current_room});
    },
    disconnected_server() {
      this.clickButton("disconncect client: " + this.uid)
      this.$socket.disconnect();
    },
    conncected_server() {
      this.resp = this.resp + '\n connected';
      this.$socket.connect();
    },
    broadcast_room(message) {
      this.$socket.emit("broadcast_room_message", {data: message, room: this.current_room})
    }
  },
})
</script>


